.tekstikentta{
    width: 100%;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    background-color: white;
    background-position: 10px 10px; 
    background-repeat: no-repeat;
    padding: 8px 8px 8px 8px;
    font-family: 'Courier New', Courier, monospace;
    font-size: 24px;
}

